@import url(https://fonts.googleapis.com/css2?family=Akaya+Kanadaka&family=Dancing+Script:wght@700&display=swap);
body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu',
        'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

body {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Helvetica, Arial, sans-serif,
        'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol';
    font-size: 14px;
    line-height: 1.5;
    color: #24292e;
    background-color: #fff;
}

.app {
    flex-direction: column;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
}

input.error {
    border-color: red;
}

.input-feedback {
    color: red;
    height: 5px;
    margin-top: -12px;
}
.background {
    background-image: url(/static/media/구름.e275b0f3.png);
    background-size: auto;
}

.menu {
    padding: 0 20px;
    border-bottom: solid 1px #e8e8e8;
    overflow: auto;
    box-shadow: 0 0 15px #bdbdbd;
    background-color: white;
    height: 70px;
}

.menu__logo {
    width: 170px;
    height: 50px;
    float: left;
    font-family: 'Dancing Script', cursive;
}

.menu__logo a {
    display: inline-block;
    font-size: 25px;
    padding: 15px 20px;
    color: #2f9d27;
    height: 65px;
}

.menu__container .ant-menu-item {
    padding: 30px 5px;
}

.menu__container .ant-menu-submenu-title {
    padding: 50px 20px;
}

.menu__container .ant-menu-item a,
.menu__container .ant-menu-submenu-title a {
    padding: 0px 15px;
}

.menu__container .ant-menu-horizontal {
    padding-top: 12px;
    border-bottom: none;
}

.menu__container .menu_left {
    float: left;
}

.menu__container .menu_rigth {
    float: right;
}

.menu__mobile-button {
    float: right;
    height: 32px;
    padding: 6px;
    margin-top: 8px;
    display: none !important; /* use of important to overwrite ant-btn */
    background: #3e91f7;
}

.menu_drawer .ant-drawer-body {
    padding: 0 !important;
}

/* align header of Drawer with header of page */
.menu_drawer .ant-drawer-header {
    padding: 14px 24px !important;
}

@media (max-width: 767px) {
    .menu__mobile-button {
        display: inline-block !important;
    }

    .menu_left,
    .menu_rigth {
        display: none;
    }

    .menu__logo a {
        margin-left: -20px;
    }

    .menu__container .ant-menu-item,
    .menu__container .ant-menu-submenu-title {
        padding: 1px 20px;
    }

    .menu__logo a {
        padding: 10px 20px;
    }
}

